<template>
    <div class="container">
        <div class="row  justify-content-center text-center">
            <div class="col-md-12">
            <h1>Iniciar Seccion</h1>
            </div>
            <div class="d-flex justify-content-center">
                <form @submit.prevent="login">
                    <input placeholder="Email" type="email" class="form-control my-2" v-model="usuario.email">
                    <input placeholder="Contraseña" type="text" class="form-control my-2" v-model="usuario.pass">
                    <button type="submit" class="btn btn-primary">Acceder</button>
                </form>
            </div>
            <div class="col-md-12" v-if="mensaje != ''">
                <p>{{mensaje}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    data(){
        return {
            usuario: {email: 'usuario5@gmail.com', pass: '123123'},
            mensaje: ''
        }
    },
    methods: {
        ...mapActions(['guardarUsuario']),
        login(){
          this.axios.post('/login', this.usuario)
            .then(res => {
              console.log(res.data);
              const token = res.data.token;
              this.guardarUsuario(token)
            })
            .catch(err => {
              console.log(err.response.data.mensaje);
              this.mensaje = err.response.data.mensaje;
              
            })
        }
    }
}
</script>

<style scoped>
h1{
    color: white;
    font-weight: 600;
}
</style>>
